import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'random',
      component: r => require(['@/components/dish/random.vue'], r)
    },
    {
      path: '/add',
      name: 'add',
      component: r => require(['@/components/dish/add'], r)
    },
    {
      path: '/register',
      name: 'register',
      component: r => require(['@/components/user/register'], r)
    },
    {
      path: '/login',
      name: 'login',
      component: r => require(['@/components/user/login'], r)
    },
    {
      path: '/profile',
      name: 'profile',
      component: r => require(['@/components/user/profile'], r)
    }
    ,
    {
      path: '/test',
      name: 'test',
      component: r => require(['@/components/test'], r)
    }

  ],
  base: "/eat/"
})



