<template>
    <div class="login-status">
        <div v-show="username">你好：<span class="username">{{username}}</span>
            <el-button type="primary" size="mini" @click="logout">退出</el-button>
        </div>
        <router-link to="/login" v-show="!username">
            <el-button type="primary" size="mini">登录</el-button>
        </router-link>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                username: null
            }
        },
        methods: {
            checkLogin() {
                this.axios.get(apiUrl + '/eatApi/user/checkLogin').then(res => {
                    var data = res.data
                    if (data.msgcode == 1) {
                        this.username = data.username
                        bus.username = data.username
                    } else {
                        this.username = null
                    }
                }).catch(err => {
                    this.$message({
                        message: '登录状态验证失败',
                        type: 'warning'
                    })
                    console.log(err)
                })
            },
            logout() {
                this.axios.post(apiUrl + '/eatApi/user/logout').then(res => {
                    var data = res.data
                    if (data.msgcode == 1) {
                        this.username = null
                        bus.username = null
                    } else {
                        this.$message({
                            message: '退出失败，请重试',
                            type: 'warning'
                        })
                    }
                }).catch(err => {
                    this.$message({
                        message: '退出失败，请重试',
                        type: 'warning'
                    })
                    console.log(err)
                })
            }
        },
        mounted() {
            bus.$on('checkLogin', () => {
                this.checkLogin()
            })
        }
    }

</script>
<style scoped>
    .username {
        color: #58B7FF;
    }
</style>


